@import "../variables";

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  @media (max-width: 576px) {
    padding: 20px 0;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 576px) {
      flex-wrap: wrap;
      gap: 15px;
    }
  }

  a:has(.header__logo) {
    @media (max-width: 576px) {
      flex: 1 1 48%;
      max-width: max-content;
    }
  }


  &__logo {
    display: flex;

    @media (max-width: 576px) {
      gap: 15px;
    }

    div {
      text-align: left;
    }

    img {
      margin-right: 15px;

      @media (max-width: 992px) {
        margin-right: 0;
      }
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;

      @media (max-width: 992px) {
        display: none;
      }

      // @media (max-width: 576px) {
      //   display: block;
      // }
    }

    p {
      color: #7b7b7b;

      @media (max-width: 992px) {
        display: none;
      }

      // @media (max-width: 576px) {
      //   display: block;
      // }
    }
  }

  &__cart {
    @media (max-width: 576px) {
      flex: 1 1 48%;
      max-width: max-content;
    }
  }
}
