@import "../variables";

.categories {
  width: 100%;

  ul {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;

    @media (max-width: 992px) {
      margin-bottom: 10px;
      flex-wrap: initial;
      overflow: auto;
      width: 100%;
    }

    li {
      background-color: #f9f9f9;
      padding: 13px 30px;
      border-radius: 30px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;
      @include noselect();

      &:hover {
        background-color: darken(#f9f9f9, 2%);
      }

      &:active {
        background-color: darken(#f9f9f9, 5%);
      }

      &.active {
        background-color: #282828;
        color: #fff;
      }

      @media (max-width: 576px) {
        padding: 6px 15px;
      }
    }
  }
}
