@import "./variables";
@import "./libs/normalize";
@import "./fonts";
@import "./components/all";

body {
  background-color: $background;
}

.wrapper {
  height: 100%;
  background-color: #fff;
  margin: 0 auto;
  border-radius: 10px;
  max-width: 1400px;
  padding: 5px 0;
}

.content {
  padding: 40px 0;

  @media (max-width: 576px) {
    padding: 20px 0;
  }

  &__title {
    margin: 35px 0;

    @media (max-width: 576px) {
      margin: 15px 0;
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    grid-template-rows: repeat(2, 1fr);
    grid-auto-rows: 1fr;
    gap: 30px 10px;
    justify-items: center;
    align-content: center;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;

    @media (max-width: 992px) {
      flex-direction: column;
    }
  }
}

.container {
  max-width: $container-width;
  margin: 0 auto;
  padding: 0 15px;

  &--cart {
    max-width: 820px;
    margin: 90px auto;
    .content__title {
      margin: 0;
    }
  }
}

.cart {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    background-color: transparent;
    border: none;
    @include noselect();

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;

      @media (max-width: 430px) {
        display: none;
      }
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }

  &__item {
    display: flex;
    width: 100%;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;

    @media (max-width: 768px) {
      flex-direction: row;
      flex-wrap: wrap;
      gap: 15px;
    }

    &-img {
      display: flex;
      align-items: center;
      margin-right: 15px;
      width: 10%;

      @media (max-width: 768px) {
        flex: 1 1 45%;
        max-width: 80px;
      }

      img {
        width: 80px;
        height: 80px;

        @media (max-width: 768px) {
          margin: 0 auto;
        }
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 40%;

      @media (max-width: 768px) {
        flex: 1 1 50%;
        max-width: 100%;
      }

      h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
      }

      p {
        font-size: 18px;
        color: #8d8d8d;
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 13%;

      @media (max-width: 768px) {
        justify-content: center;
        gap: 15px;
        flex: 1 1 33%;
        // max-width: 120px;
        margin: 0 auto;
      }

      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }
      }

      b {
        font-size: 22px;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 33%;

      @media (max-width: 768px) {
        flex: 1 1 33%;
      }

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 4%;

      @media (max-width: 768px) {
        justify-content: center;
        flex: 1 1 20%;
      }

      .button {
        border-color: darken($color: $gray-line, $amount: 10);
      }

      svg {
        transform: rotate(45deg);

        path {
          fill: darken($color: $gray-line, $amount: 15);
        }
      }

      .button {
        svg {
          width: 11.5px;
          height: 11.5px;
          position: relative;
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;

      @media (max-width: 450px) {
        flex-direction: column;
      }

      span {
        font-size: 22px;

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;

        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
      }

      @media (max-width: 450px) {
        flex-direction: column;
        gap: 15px;
        & .button {
          width: 100%;

          &.go-back-btn {
            order: 1;
            padding: 16px;
          }
        }
      }
    }
  }

  &--empty {
    margin: 0 auto;
    // width: 560px;
    text-align: center;

    h2 {
      font-size: 32px;
      margin-bottom: 10px;
    }

    p {
      font-size: 18px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 300px;
      margin: 45px auto 60px;
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
}

.input-search {
  width: 250px;
  height: 51px;
  padding: 12px;
  border: 1px solid #7b7b7b;
  border-radius: 10px;
  font-size: 16px;

  transition: all 0.3s ease-in;

  &:focus,
  &:active {
    border: 1px solid #fe5f1e;
  }

  &::placeholder {
    opacity: 0.5;
  }

  @media (max-width: 576px) {
    flex: 1 1 100%;
    order: 1;
  }
}

.loader {
  position: relative;
  display: inline-block;
  border: 24px solid #fff;
  border-radius: 50%;
  transform: rotate(45deg);
  animation: pieFill 3s linear infinite;
}

@keyframes pieFill {
  0%,
  19% {
    border-color: #fff #fff #fff #fff;
  }
  20%,
  39% {
    border-color: #ff3d00 #fff #fff #fff;
  }
  40%,
  59% {
    border-color: #ff3d00 #ff3d00 #fff #fff;
  }
  60%,
  79% {
    border-color: #ff3d00 #ff3d00 #ff3d00 #fff;
  }
  80%,
  100% {
    border-color: #ff3d00 #ff3d00 #ff3d00 #ff3d00;
  }
}

.not-found {
  h2 {
    margin-bottom: 15px;
  }

  p {
    margin-bottom: 15px;

    font-size: 18px;
    line-height: 145.4%;
    letter-spacing: 0.01em;
    color: #777777;
  }

  img {
    display: block;
    max-width: 300px;
    margin: 45px auto 60px;
  }

  a {
    padding: 12px 0 14px;
    width: 230px;
    margin: 0 auto;
    font-weight: 600;
    font-size: 18px;
  }
}
