@import "../variables";

.pizza-block {
  width: 280px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &__image {
    width: 260px;
  }

  &__title {
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1%;
    margin-bottom: 5px;
  }

  &__selector {
    margin-top: auto;
    width: 250px;
    display: flex;
    background-color: #f3f3f3;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;

    ul {
      display: flex;
      flex: 1;

      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        @include noselect();
        &.active {
          background: #ffffff;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 5px;
          cursor: auto;
        }
      }
    }
  }

  &__bottom {
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
  }

  &__descr {
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
    span {
      font-weight: bold;
    }
  }

  &--full {
    margin: 0 auto;
    max-width: 300px;
    width: auto;
    gap: 10px;

    img {
      width: auto;
      max-width: 100%;
    }

    .pizza-block__descr {
      font-size: 20px;
    }

    & + .button {
      padding: 12px 0 14px;
      width: 230px;
      margin: 25px auto 0;
      font-weight: 600;
      font-size: 18px;
    }
  }
}
